.app {
  text-align: center;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  /* Responsive columns */
  grid-template-rows: repeat(4, 1fr);
  /* Responsive rows */
  gap: 10px;
  justify-content: center;
  align-items: center;
  width: 100%;
  /* Full width */
  max-width: 100vw;
  /* Prevent overflow */
  height: 80vh;
  /* Set height relative to viewport */
}

.square {
  width: 100%;
  /* Full width of grid cell */
  height: 0;
  padding-bottom: 100%;
  /* This makes the square responsive (aspect ratio 1:1) */
  background-color: lightblue;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 6vw;
  /* Scale font size with viewport */
  font-weight: bold;
  user-select: none;
  transition: background-color 0.3s, transform 0.3s;
  cursor: pointer;
}

.square.flipped {
  background-color: lightcoral;
  transform: rotateY(180deg);
  /* Flip effect */
}

.square.no-animation {
  transition: none;
  /* Disable transition while dragging */
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  .square {
    font-size: 8vw;
    /* Increase font size for smaller screens */
  }

  .grid {
    gap: 5px;
    /* Reduce gap for smaller screens */
  }
}

@media (max-width: 480px) {
  .square {
    font-size: 10vw;
    /* Further increase font size for very small screens */
  }

  .grid {
    gap: 3px;
    /* Reduce gap even more */
  }
}